    flex-direction: column;
    align-items: center;
    background-color: #E5E5E5;
    width: 100vw;
    height: 100vh;

    #mainpage {
        #balance_bar {
            display: flex;
            flex-direction:row {}
            justify-content: left;
            align-items: center;
            width: 100vw;
            height: 15vh;
            left: 10vw;
            color: #E5E5E5;
            #contain {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                margin-left: 10.43vw;


                .balance {
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    align-items: center;
                    top: 7.39vh;
                    left: 10.13vw;
                    height: 9.33vh;
                    width: 27.46vw; }

                t {
                    display: flex;
                    flex-direction:left {}
                    font-size: 10px;
                    width: 15vw;
                    height: 8vh;
                    button: 0vh;
                    left: 1.013vw; } } }
        #project_block {
            display: flex;
            flex-direction:row {}
            justify-content: center;
            align-items: center;
            width: 100vw;
            height: 37.44vh; }
        #detail_block {}
        .add_button {} }

    #account {
        display: none; }

    #accounting {
        display: none; }

    #barcode {
        display: none; }

    #project {
        display: none; }

    #navbar {
        background-color: white;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        width: 100vw;
        height: 10.59vh;
        position: absolute;
        bottom: 0;

        img {
            margin: 0 8vw 0 0; }  //30px

        img:nth-child(5) {
            margin: 0; } } }
